header {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    background: #1f1f5d;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: flex-end;
            justify-content: flex-end;
    -webkit-align-items: center;
            align-items: center;
    transition: 1s;
    font-family: 'Raleway', sans-serif;
    z-index: 40;
}

.header-active {
    height: 80px;
}

header .banner {
    height: 100%;
    width: 100%;
    position: absolute;
    top: 0;
    left: 0px;
    background: linear-gradient(rgba(37, 37, 110, 0.88), rgba(31,31,93, 0.95)),
    url(/static/media/sgu.2d78861b.jpg) center/ cover;
    transition: 1s;
}

.header-active .banner {
    background: #1f1f5d;
}

header .logo {
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    font-size: 5vw;
    color: #fff;
    font-weight: 700;
    transition: 1s;
    z-index: 1;
}

.header-active .logo {
    position: absolute;
    font-size: 1.5em;
    left: 100px;
    -webkit-transform: translate(0, -50%);
            transform: translate(0, -50%);
    max-width: 350px;
}

nav {
    padding: 0 85px;
    position: relative;
    display: -webkit-flex;
    display: flex;
    z-index: 1;
}

nav ul {
    position: relative;
    display: -webkit-flex;
    display: flex;
    transition: 0.25s;
    -webkit-transform: translateX(100px);
            transform: translateX(100px);
    opacity: 0;
}

.header-active nav ul {
    -webkit-transform: translateX(0);
            transform: translateX(0);
    opacity: 1;
    transition-delay: 0.75s;
    opacity: 1;
    visibility: visible;
}

nav li {
    list-style: none;
}

nav a {
    color: #fff;
    display: inline-block;
    padding: 10px 15px;
    font-size: 1.2em;
    text-decoration: none;
}

nav a:hover {
    color: rgba(106, 90, 197, 0.925);
}

@media (max-width:1400px) {
    .header-active .logo {
        left: 10px;
        width: 40%;
    }

    nav {
        padding: 0 15px;
    }

    nav a {
        padding: 10px 10px;
    }
  }

@media (max-width:1200px) {
    .header-active .logo {
        left: 20px;
    }

    .header-active .banner {
        opacity: 0;
    }
    nav ul {
        display: none;
        opacity: 0;
        visibility: hidden;
    }

    nav.active ul{
        position: fixed;
        top: 80px;
        left: 0;
        width: 100%;
        height: calc(100% - 80px);
        background: #1f1f5d;
        display: -webkit-flex;
        display: flex;
        -webkit-justify-content: center;
                justify-content: center;
        -webkit-align-items: center;
                align-items: center;
        -webkit-flex-direction: column;
                flex-direction: column;
        margin: 0;
        padding: 0;
    }

    nav a {
        font-size: 1.5em;
    }


    .header-active .burger {
        position: fixed;
        top: 21px;
        right: 16px;
        width: 2rem;
        height: 2rem;
        display: -webkit-flex;
        display: flex;
        -webkit-justify-content: space-around;
                justify-content: space-around;
        -webkit-flex-flow: column;
                flex-flow: column;
        cursor: pointer;
        transition: all 0.3s linear;
    }
    .header-active .burger .burger-item:nth-child(1) {
        -webkit-transform: rotate(0);
                transform: rotate(0);
    }
    .header-active .burger .burger-item:nth-child(2) {
        -webkit-transform: translateX(0);
                transform: translateX(0);
        opacity: 1;
    }
    .header-active .burger .burger-item:nth-child(3) {
        -webkit-transform: rotate(0);
                transform: rotate(0);
    }

    .header-active .burger-active {
        position: fixed;
        top: 21px;
        right: 16px;
        width: 2rem;
        height: 2rem;
        display: -webkit-flex;
        display: flex;
        -webkit-justify-content: space-around;
                justify-content: space-around;
        -webkit-flex-flow: column;
                flex-flow: column;
        transition: all 0.3s linear;
    }
    .header-active .burger-active .burger-item:nth-child(1) {
        -webkit-transform: rotate(45deg);
                transform: rotate(45deg);
    }
    .header-active .burger-active .burger-item:nth-child(2) {
        -webkit-transform: translateX(100%);
                transform: translateX(100%);
        opacity: 0;
    }
    .header-active .burger-active .burger-item:nth-child(3) {
        -webkit-transform: rotate(-45deg);
                transform: rotate(-45deg);
    }

    .header-active .burger-item {
        width: 2rem;
        height: 0.25rem;
        background-color: #fff;
        border-radius: 10px;
        -webkit-transform-origin: 1px;
                transform-origin: 1px;
        transition: all 0.3s linear;
    }
}

@media (max-width:767px) {
    .header-active .logo {
        left: 10px;
        width: 50%;
        font-size: 1.2em;
    }
}
